*{
    margin:0px;
    padding:0px;
    box-sizing:border-box;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 100;
}
body{
    background-color: rgba(128, 128, 128, 0.342);
}
h1{
    color:yellow;
}
.Navbar_div
{
    text-align: center;
    padding:30px;
    background-color: rgba(128, 128, 128, 0.265);
    font-size:30px;
}
.Navbar_div img{
    float: left;
}
.form_container{
    
    width:40%;
    margin:30px auto;
    padding:30px;
    background-color: white;
    border-radius: 10px;
}
.form_input,.form_label{
    display:inline;
    width:100%;
    border-radius: 10px;
    padding:5px;
   margin:10px auto;
  
}
.form_label{
    margin:10px auto;
}
.btn{
    display: block;
    padding:5px 10px;
    margin:10px auto;
    text-align: center;
    background-color: rgb(4, 131, 242);
    border: 1px solid black;
    border-radius: 10px;
    color:white;
    font-size: 20px;

}
.adminbtn{
    display: inline;
    margin:30px;
    
}
.admin_container{
    background-color: white;
    color:black;
    border-radius: 20px;
    padding:30px;
    margin:30px auto;
    text-align: center;
    width:90%;
}
.adminflex_container{
    width:80%;
    display: flex;
    justify-content:space-around;
}
.heading{
    width:80%;
    justify-content: space-around;
}

.adminbutton_container{
    width:10%;
    display: flex;
 
}
.adminflex_container p
{
    font-weight: bold;
    font-size: 20px;
    text-transform: uppercase;
    text-align: right;
}

.items{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.admin_orders,.admin_orders_2 p{
    text-align: left;
    font-size: 23px;
    text-transform: capitalize;
    
}
.admin_orders p,.admin_orders_2 p{
    font-weight: bold;
    
}
.admin_orders p svg{
    margin-left:20px;
}
.admin_orders_2 p span{
    text-decoration: underline;
    margin-right: 20px;
   
}

